var birthDay = new Date("09/17/2016");  // 这里填写网站第一次运行日期
var now = new Date();
var duration = now.getTime() - birthDay.getTime(); 
var total= Math.floor(duration / (1000 * 60 * 60 * 24));

let data = {
	"title":"统计",
	"total":total,
	"monthUpdate":"8",
	"allArticle":"60",
}

module.exports =data;


